___CSS_LOADER_EXPORT___.push([module.id, `.card_news {
    margin: 10px;
    border-radius: 20px;
    display: flex;
    height: 248px;
    background-color: #ffffff;
    box-shadow: 0 10px 8px rgba(0, 0, 0, 0.1);
  }

  .card_img img{
    text-align: center;
    width: 200px;
    padding: 5px;
    border-radius: 20px;
  }

  .desc_news, .title_news { 
    padding: 10px;
    margin-left: 20px;
  }
  .selengkapnya{
    display: flex;
    align-items: center;
    font-size: 10px;
    height: 40px;
    padding: 1px 6px;
    background-color: #191818;
    border-radius: 10px;
    margin-right: 20px;
    margin-top: auto;
    margin-bottom: 10px;
  }

  .selengkapnya a {
    color: white;
  }

  .newsheader {
    height: 8rem;
    background-color: crimson;
    display: flex;
    align-items: center;
  }
  .newsheader h2 {
    margin: 20px;
    color: white;
  }

  .trending {
    margin: 20px;
    font-weight:bolder;
  }

  .searchbar {
    font-size: 14px;
    font-family: arial, sans-serif;
    color: #202124;
    display: flex;
    z-index: 3;
    height: 44px;
    background: white;
    border: 1px solid #dfe1e5;
    box-shadow: none;
    border-radius: 24px;
    margin: 0 auto;
    width: auto;
    width: 70%;
  }
  
  .searchbar:hover {
    box-shadow: 0 1px 6px rgb(32 33 36 / 28%);
    border-color: rgba(223,225,229,0);
  }
  
  .searchbar-wrapper {
    flex: 1;
    display: flex;
    padding: 5px 8px 0 14px;
  }
  
  .searchbar-left {
    font-size: 14px;
    font-family: arial, sans-serif;
    color: #202124;
    display: flex;
    align-items: center;
    padding-right: 13px;
    margin-top: -5px;
  }
  
  .search-icon-wrapper {
    margin: auto;
  }
  
  .search-icon {
    margin-top: 3px;
    color: #9aa0a6;
    height: 20px;
    line-height: 20px;
    width: 20px;
  }
  
  .searchbar-icon {
    display: inline-block;
    fill: currentColor;
    height: 24px;
    line-height: 24px;
    position: relative;
    width: 24px;
  }
  
  .searchbar-center {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
  }
  
  .searchbar-input-spacer {
    color: transparent;
    flex: 100%;
    white-space: pre;
    height: 34px;
    font-size: 16px;
  }
  
  .searchbar-input {
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
    color: rgba(0, 0, 0, .87);
    word-wrap: break-word;
    outline: none;
    display: flex;
    flex: 100%;
    margin-top: -37px;
    height: 34px;
    font-size: 16px;
    max-width: 100%;
    width: 100%;
  }
  
  .searchbar-right {
    display: flex;
    flex: 0 0 auto;
    margin-top: -5px;
    align-items: stretch;
    flex-direction: row
  }
  
  .searchbar-clear-icon {
    margin-right: 12px
  }
  
  .voice-search {
    flex: 1 0 auto;
    display: flex;
    cursor: pointer;
    align-items: center;
    border: 0;
    background: transparent;
    outline: none;
    padding: 0 8px;
    width: 2.8em;
  }`, "",{"version":3,"sources":["webpack://./src/styles/news.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,mBAAmB;IACnB,aAAa;IACb,aAAa;IACb,yBAAyB;IACzB,yCAAyC;EAC3C;;EAEA;IACE,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,mBAAmB;EACrB;;EAEA;IACE,aAAa;IACb,iBAAiB;EACnB;EACA;IACE,aAAa;IACb,mBAAmB;IACnB,eAAe;IACf,YAAY;IACZ,gBAAgB;IAChB,yBAAyB;IACzB,mBAAmB;IACnB,kBAAkB;IAClB,gBAAgB;IAChB,mBAAmB;EACrB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,YAAY;IACZ,yBAAyB;IACzB,aAAa;IACb,mBAAmB;EACrB;EACA;IACE,YAAY;IACZ,YAAY;EACd;;EAEA;IACE,YAAY;IACZ,kBAAkB;EACpB;;EAEA;IACE,eAAe;IACf,8BAA8B;IAC9B,cAAc;IACd,aAAa;IACb,UAAU;IACV,YAAY;IACZ,iBAAiB;IACjB,yBAAyB;IACzB,gBAAgB;IAChB,mBAAmB;IACnB,cAAc;IACd,WAAW;IACX,UAAU;EACZ;;EAEA;IACE,yCAAyC;IACzC,iCAAiC;EACnC;;EAEA;IACE,OAAO;IACP,aAAa;IACb,uBAAuB;EACzB;;EAEA;IACE,eAAe;IACf,8BAA8B;IAC9B,cAAc;IACd,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,gBAAgB;EAClB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,eAAe;IACf,cAAc;IACd,YAAY;IACZ,iBAAiB;IACjB,WAAW;EACb;;EAEA;IACE,qBAAqB;IACrB,kBAAkB;IAClB,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,WAAW;EACb;;EAEA;IACE,aAAa;IACb,OAAO;IACP,eAAe;EACjB;;EAEA;IACE,kBAAkB;IAClB,UAAU;IACV,gBAAgB;IAChB,YAAY;IACZ,eAAe;EACjB;;EAEA;IACE,6BAA6B;IAC7B,YAAY;IACZ,SAAS;IACT,UAAU;IACV,yBAAyB;IACzB,qBAAqB;IACrB,aAAa;IACb,aAAa;IACb,UAAU;IACV,iBAAiB;IACjB,YAAY;IACZ,eAAe;IACf,eAAe;IACf,WAAW;EACb;;EAEA;IACE,aAAa;IACb,cAAc;IACd,gBAAgB;IAChB,oBAAoB;IACpB;EACF;;EAEA;IACE;EACF;;EAEA;IACE,cAAc;IACd,aAAa;IACb,eAAe;IACf,mBAAmB;IACnB,SAAS;IACT,uBAAuB;IACvB,aAAa;IACb,cAAc;IACd,YAAY;EACd","sourcesContent":[".card_news {\r\n    margin: 10px;\r\n    border-radius: 20px;\r\n    display: flex;\r\n    height: 248px;\r\n    background-color: #ffffff;\r\n    box-shadow: 0 10px 8px rgba(0, 0, 0, 0.1);\r\n  }\r\n\r\n  .card_img img{\r\n    text-align: center;\r\n    width: 200px;\r\n    padding: 5px;\r\n    border-radius: 20px;\r\n  }\r\n\r\n  .desc_news, .title_news { \r\n    padding: 10px;\r\n    margin-left: 20px;\r\n  }\r\n  .selengkapnya{\r\n    display: flex;\r\n    align-items: center;\r\n 