.jumbtrone-tentangkami p {
  font-size: 18px;
  color: #EE2B47;
}

.logo-wrap-tentangkami {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo-wrap-tentangkami p {
  text-align: center;
  width: 200px;
  font-weight: bold;
  color: #EE2B47;
}

.logo-tentangkami {
  width: 325px;
  height: 82px;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  margin-bottom: 10px;
}

.team-wrap-tentangkami {
  width: 80%;
  margin: auto;
  padding-bottom: 100px;
}

.team-wrap-tentangkami h2 {
  text-align: center;
  color: #EE2B47;
  font-size: 24px;
}

.team-wrap-tentangkami hr {
  border: none;
  margin: 20px auto 40px auto;
  width: 150px;
  height: 5px;
  background-color: #EE2B47;
}

.team-group {
  display: flex;
  gap: 20px;
  justify-content: space-evenly;
}

.team-item {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 22%;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 28px 10px -20px rgba(0, 0, 0, 0.7);
  transition: 0.5s;
  border: 1px solid black;
}

.team-item:hover {
  transform: translateY(-40px);
  box-shadow: 0px 80px 10px -40px rgba(0, 0, 0, 0.18);
}

.team-header {
  height: 80px;
  background-color: #EE2B47;
}

.team-image {
  display: flex;
  align-self: center;
  position: absolute;
  top: 30px;
  width: 90px;
  height: 90px;
  background-color: grey;
  border-radius: 50%;
  overflow: hidden;
  background-size: cover;
}

.team-body {
  box-sizing: border-box;
  padding-top: 40px;
  height: 210px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.team-body p {
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 10px;
}

.team-body .sosmed {
  display: flex;
  gap: 15px;
}

.team-body .sosmed a {
  color: black;
  font-size: 28px;
}

.team1 {
  background-size: 220%;
  background-position: -50px -20px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.team2 {
